.foot_list{
    margin-top: 15px;
	width: 100%;
	display: flex;
	justify-content: center;
}
.pagination {
    display: inline-block;
}
.information{
    margin-top: 8px;
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
}

.information a, p {
    font-weight: 500;
    color: black;
    text-decoration: none;
}
.information a {
    margin-bottom: 10px;
}
.dark .information a {
    color: rgb(167, 167, 167);
}

.information p {
    color: rgb(160, 0, 0);
}

.pagination svg{
    margin: 0 -5px;
}

.pagination a {
    cursor: context-menu;
    color: black;
    float: left;
    padding: 8px 16px;
    text-decoration: none;
  }
  .pagination a.active {
    background-color: #FF4B2B;
    color: white;
    font-weight: bold;
  }
  
  .back{
    cursor: pointer;
    margin: 0 auto 20px;
    width: 30%;
    font-size: x-large;
    color: #FF4B2B;
    text-shadow: 0 2px 5px rgba(74, 0, 0, 0.193), 
			0 2px 5px rgba(48, 0, 0, 0.153);
  }
.disabled{
    background-color: rgba(207, 207, 207, 0.379);
    padding: 20px;
}
.disabled svg{
    color: rgba(110, 110, 110, 0.318); 
}
.dark .pagination a{
    color: #ddd;
}

.dark .disabled{
    background-color: rgba(209, 1, 1, 0.379);
    
}

.pagination a:hover:not(.disabled) {
    background-color: #ddd;   
    cursor: pointer;
    color: black;
}


.dark.foot_list .pagination a:hover{
    cursor: context-menu;
    color: rgb(222, 10, 10);
}